import React from 'react';
import { Message as SUIMessage, } from 'semantic-ui-react';
import styled from 'styled-components';
const colors = {
    error: (props) => [
        props.theme.redOrange,
        props.theme.coral,
    ],
    info: (props) => [
        props.theme.robinEggBlue,
        props.theme.neonBlue,
    ],
    success: (props) => [
        props.theme.lightBlue1,
        props.theme.purple,
    ],
    warning: (props) => [
        props.theme.orangeYellow,
        props.theme.tangerine,
    ],
};
/**
 * Alert Bar CSS Gradients have 2 colors, get the color at index `index`.
 * @param index - The index of the gradient color.
 */
function gradientColor(index) {
    return function (props) {
        // Check if props.{error, info, warning} is set.
        const alertType = ['error', 'info', 'warning'].find((type) => props[type]);
        if (alertType) {
            return colors[alertType](props)[index];
        }
        // By default we return the success gradient
        return colors.success(props)[index];
    };
}
const StyledAlert = styled(SUIMessage) `
  &&& {
    background-image: linear-gradient(
      107deg,
      ${gradientColor(0)},
      ${gradientColor(1)} 71%
    );
    bottom: 0;
    left: 0;
    box-shadow: none;
    color: ${(props) => props.theme.white};
    padding-bottom: 2rem;
    padding-left: 4rem;
    padding-right: 6rem;
    position: fixed;
    width: 100%;
  }
`;
export function Alert(props) {
    return React.createElement(StyledAlert, Object.assign({}, props));
}
